import React, { Component } from 'react';
import { connect } from 'react-redux';
import * as actionsAsync from '../../../store/actions/actionsAsync';
import Result from './Result/Result';
import classes from './Results.module.scss';
import Spinner from '../../UI/Spinner/Spinner';
import Message from './../../UI/Message/Message';

class Results extends Component {
  constructor(props) {
    super(props);
    this.state = {
      pages: 3
    };
  }

  ResultClickHandler = () => {};

  render() {
    let Results = <Message>Search for a recipe to load all the results</Message>; // <p> Please search for an item</p>;

    if (this.props.loadingResults) {
      Results = <Spinner />;
    } else if (this.props.searchResults.length !== 0) {
      Results = this.props.searchResults.map(result => {
        return (
          <Result
            key={result.recipe_id}
            image={result.image_url}
            title={result.title}
            author={result.publisher}
            imageText={result.title}
            handleRecipeSelect={() => this.props.onfetchRecipe(result.recipe_id)}
            highlight={
              this.props.currentRecipe
                ? this.props.currentRecipe.recipe_id === result.recipe_id
                : false
            }
          />
        );
      });
    }

    return <div className={classes.Container}>{Results}</div>;
  }
}

const mapStateToProps = state => {
  return {
    searchResults: state.searchResults,
    currentRecipe: state.currentRecipe,
    loadingResults: state.loadingResults
  };
};

const mapDispatchToProps = dispatch => {
  return {
    onfetchRecipe: id => dispatch(actionsAsync.fetchRecipe(id))
  };
};

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(Results);
